<template>
  <div class="album_container" @showIdxPage="showIdxPage">
    <div class="top_container">
        <!-- 头部箭头 -->
      <div class="top_bar">
        <van-icon name="arrow-left" size="25px" @click="goback"  color="#fff" />
      </div>
      <!-- top info -->
      <div class="top_info_Box"  :data='detailtoplist'>
        <div class="left-img"  >
            <img :src="detailtoplist.coverImgUrl" alt="">
            <!-- 播放次数 -->
            <div class="top-icon">
                <van-icon name="play" color="#fff" />
            <span class="count">{{detailtoplist.playCount|| '555' | strdata}}亿</span>
            </div>
        </div>

        <!-- right-box -->
        <div class="right-box">
          <div class="title-top">{{detailtoplist.name}}</div>
          <div class="middle_box">
              <van-icon name="audio"  size="25px"/>
              <span class="middle-text">网易云音乐  ></span>
          </div>
          <div class="bottm-text">
              {{detailtoplist.description}} 
          </div>
        </div>
      </div>
    </div>
	
	<!-- songlist -->
	<div class="songlix-box">
	  <li v-for="(item,index) in songdeial.tracks|| songdeial.songs" @click="add(item.id)">
		  <span class="indexspan">{{index+1}}</span>
		  <span>
			  <p class="songtitle-p"> {{item.name}}</p>
			  <p class="songtitle-p1">
			  <span v-for="(item2,index2) in item.ar" :key="index2">{{item2.name}}-</span>
			  <span class="text-more">{{item.al.name}}</span>
			  </p>
		  </span>
	  </li>
	</div>
	
	<div class="audiobox" v-show="flag" :data="onesong">
	 <audio :src="onesong.url" autoplay></audio>
	</div>
  </div>
</template>

<script>

export default {
  data() {
    return {
    // 头部数据
    detailtoplist:[],
    //歌单数据
    songlist:[],
	song:[],
	songdeial:[],
	onesong:[],
	flag:false
    }
  },
  created() {
  this.getdetail()
  },
  methods: {
    goback() {
      this.$router.go(-1);//返回上一层
    },
    async getdetail(){
    const {data:res}= await  this.$http.get('top/list?idx=0')
    this.detailtoplist=res.playlist
    this.songlist=res.privileges
	this.songdeial=res.playlist

    },
	async add(id){
	const {data:res}=await	this.$http.get('song/url?id='+id)
	this.onesong=res.data[0]
	},
	showIdxPage(id){
		console.log(id)
	}
  }
}
</script>

<style lang="less" scoped>
.album_container {
    margin: 0;
    padding: 0;
  .top_container {
      width: 100%;
      height: 300px;
      background: #ee5253;
      padding: 0 !important;
    .top_bar {
      height: 50px;
      display: flex;
      align-items: center;
    }
   .top_info_Box{
       padding: 0px 10px;
       display: flex;
      .left-img{
          width: 135px;
          height: 135px;
          position: relative;
          .top-icon{
              color: #fff;
              position: absolute;
              top: 0;
              right: 0px;
              font-size: 11px;
              height: 15px;
              display: flex;
              align-items: center;
          } 
      }
      img{
      width: 100%;
      height: 100%;
      }
      .right-box{
          padding-left: 30px;
          flex: 1;
          .title-top{
              font-size: 20px;
              color: #fff;
              overflow: hidden;
              height: 28px;
              line-height: 28px;
          }
          .middle_box{
              color: #ccc;
              height: 52px;
              line-height: 52px;
              font-size: 14px;
              .van-icon{
                  vertical-align: middle;
              }
              .middle-text{
                  padding-left: 15px;
              }
          }
          .bottm-text{
              color: #ccc;
               font-size: 15px;
               
          }
      }
   }
  }
  .songlix-box{
	  padding: 6px 12px;
	  li{
		  height: 53px;
		  display: flex;
	      align-items: center;
		  .indexspan{
			  padding-right: 15px;
			  color: #999;
		  }
		  .songtitle-p{
			  height: 20px;
			  font-size: 14px;
			  color: #333;
			  width: 300px;
			  white-space: nowrap;
			  overflow: hidden;
			  text-overflow: ellipsis;
		  }
		  .songtitle-p1{
			color: #dacdcd;
			height:20px;
			font-size: 12px;
			white-space: nowrap;
			overflow: hidden;
			text-overflow: ellipsis;
			width: 300px;
		  }
	  }
  }
}
</style>
