import axios from 'axios'

import {
topList,
idxList
} from './config'

export default{
/**
	   * 调用此接口,可获取所有榜单内容摘要
	   */
  topListFn(){
	  return axios.get(topList)
  },
 /**
   * 调用此接口 , 传入数字 idx, 可获取不同排行榜
   * @param {*} idx 排行榜 idx
   */
   inxListFn(idx){
	   return axios.get(idxList,{
		   params:{
			   idx
		   }
	   })
   }
}